.contacts-section {
  // background-color: #FFF5F6;
  background-image: linear-gradient(#fff 30%, #fff5f6 70%);
  padding-bottom: 50px;
  @media screen and (min-width: 768px) {
    padding-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 120px;
  }
}

.contact-span {
  display: inline-block;
}

.contacts__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
}

.contacts__item {
  width: 280px;
  background-color: $main-white;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  padding: 40px 32px;

  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  @media screen and (min-width: 768px) {
    width: 225px;
    &:not(:last-child) {
      margin-right: 32px;
    }
  }

  @media screen and (min-width: 1280px) {
    width: 395px;
  }
}

.contact__place-type {
  text-transform: uppercase;
  background-color: $product-banana-color;
  color: $main-white;

  font-weight: 500;
  font-size: 12px;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
  line-height: 186%;
  letter-spacing: 0.04em;

  display: inline-flex;
  align-items: center;
  justify-content: center;

  padding-left: 25px;
  padding-right: 25px;
  margin-bottom: 32px;

  border-radius: 4px;

  .contacts__item:nth-child(2) & {
    background-color: $product-green-color;
  }
}

.contact__place-header,
.contact__days {
  font-weight: 500;
  font-size: 12px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
  line-height: 186%;

  letter-spacing: 0.04em;
}

.contact__place-description {
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 166%;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 183%;
  }

  color: $paragraph-color;
}

.contact__time-list {
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  margin-top: 24px;
  margin-bottom: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
}

.contact__time-item:last-child {
  margin-top: 10px;
}

.contact__days {
  display: block;
  letter-spacing: 0;
}

.contact__time {
  font-weight: 500;
  font-size: 12px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
  line-height: 186%;
  color: $hero-circle-color;
}

.contacts__link {
  display: block;

  font-weight: 500;
  font-size: 12px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
  line-height: 150%;

  transition: color 250ms ease;

  color: $paragraph-color;

  &:hover,
  &:focus {
    color: $hero-circle-color;
  }
}

.button-container {
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 767px) {
    gap: 10px;
    flex-direction: column;
    margin-top: 32px;
  }
  @media screen and (min-width: 768px) {
    gap: 15px;
    margin-top: 40px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 60px;
  }
}

.contact-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $first-accent-color;
  cursor: pointer;
  border: none;

  width: 161px;
  height: 44px;
  border-radius: 22px;

  transition: all 250ms ease;

  .button-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;
    text-align: center;
    color: $main-white;

    margin-right: 10px;
  }

  .button-icon {
    stroke: $main-white;
  }

  & + & {
    background-color: $main-white;
  }

  & + &:hover {
    background-color: $hover-color;
  }

  & + & .button-text {
    color: $first-accent-color;
  }

  & + & .button-icon {
    stroke: $first-accent-color;
  }

  // @media screen and (max-width: 767px) {
